.lookup-container {
    margin: 20px 0px;
}


.lookup-section-header {
    color: #717171;
    font-weight: 600;
    padding: 10px 8px 10px 9px;
    position: -webkit-sticky;
    position: sticky;
    top: -10px;
    display: inline-block;
    z-index: 31;
    font-size: 14px;
}


.no-data-card {
    TEXT-ALIGN: center;
    margin: 5px 0px;
    padding: 23px;
}


.report-divider {
    /*border-right: 1px solid #d2caca;*/
    /*margin-right: -15px;*/
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}


.grid-container {
    margin: 10px 0px !important;
    position: relative;
}


.avatar-details {
    font-size: 15px !important;
    font-weight: 800;
}

.section-wrapper{
    margin-bottom: 10px;
}

.section-label {
    font-size: 14px;
    margin-right: 10px;
    text-transform: capitalize;
    color: #3b2f63;
}

.section-value {
    font-size: 14px;
    font-weight: 600;
}

.section-header {
    font-weight: bold;
    font-size: 16px;
}


.section-chip {
    font-size: 13px;
    padding-top: 8px;
    padding-bottom: 8px;
}


.delete {
    float: right;
    margin-right: 15px;
    color: #ce2828;
}

.delete:hover {
    cursor: pointer;
}


.report-case-but {
    padding: 3px;
    border-radius: 5px;
    font-size: 13px!important;
}

.add-icon {
    font-size: 20px;
    position: relative;
    padding-right: 4px;
    bottom: 1px;
}
